@font-face {
  font-family: 'KorailRoundGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2212@1.0/KorailRoundGothicMedium.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
:root {
  --menu-femily: 'TheJamsil5Bold';
  --text-color: white;
  --text-size: 16px;
  --list-femily: 'KorailRoundGothicMedium';
}
.list_container {
  width: 250px;
  height: calc(100vh - 83px);
  background-color: var(--blue-color);
  border-radius: 0px 12px 12px 0px;
  float: left;
  z-index: 100;
  transition: width 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
}
.list_logo {
  padding-top: 20px;
  padding-left: 30px;
  padding-bottom: 50px;
  white-space: nowrap;
  transition: opacity 0.2s ease;
  opacity: 1;
}
.list_container.collapsed .list_logo {
  opacity: 0; 
}
.list_img {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px; 
  font-family: var(--menu-femily);
}
.list_menu {
  width: 100%; 
  padding: 0; 
  margin: 0; 
  list-style: none; 
  font-size: 18px;
  /* display: flex; 
  flex-direction: column;  */
}

.list_menu li {
  margin: 35px 15px; 
  padding: 5px 5px;
  transition: background-color 0.2s ease-in-out;
  border-radius: 8px;
}
.list_menu li:hover {
  background-color: #ffffff;
  border-radius: 8px; 
  color: #000000; 
}
.list_menu li:hover .listbar_icon {
  color: #000000; 
}
.list_menu li:hover .listbar_icon2 {
  color: #000000; 
}
.list_menu li:hover .menu-link, .list_menu li:hover .menu-link1 {
  color: #000000; 
}
.menu-link {
  display: flex; 
  align-items: center;
  gap: 30px; 
  text-decoration: none;
  color: white;
  font-size: var(--text-size); 
  font-family: var(--list-family);
  width: 100%; 
  overflow: hidden; 
  white-space: nowrap; 
}
.list_container.collapsed .menu-link, .list_container.collapsed {
  justify-content: flex-start;
}
.list_container.expanded {
  width: 250px; /* 확장 상태의 너비 */
}

.list_container.collapsed {
  width: 65px; /* 축소 상태의 너비 */
}

.toggle-btn1 {
  position: absolute;
  left: 210px;
  top: 105px;
  cursor: pointer;
  z-index: 101;
}
.toggle-btn2 {
  position: absolute;
  left: 20px;
  top: 100px;
  cursor: pointer;
  z-index: 103;
}

.listbar_icon {
  color: #ffffff;
  border-radius: 5px;
}
.listbar_icon2 {
  color: #ffffff;
  border-radius: 5px;
}
.listbar_icon:hover {
  color: #000000;
  background-color: #ffffff;
}
.list_container.admin {
  background-color: #f7b436;
}

.list_container.admin .menu-link {
  color: white;
}
